.sign-in{
	width: 500px;
	min-height: 566px;
	border: 3px solid #AD90D2;
	background-color: white;
}

.sign-in h2{
	text-align: center;
	margin-bottom: 20px;
}
#wrapper{
	min-height: 396px;
}

.wrapper-for-inputs{
	display: flex;
	flex-direction: column;
	justify-content: center;
	align-items: center;
}

.wrapper-for-inputs input{
	margin-bottom: 50px;
	height: 20px;
	width: 80%;
}

.enter{
	position: relative;
	top: 20px;
	width: 70%;
	height: 50px;
	margin-top: 80px;
	margin-top: 60px;
	margin-bottom: 60px;
	background-color: #EAEAEA;
	border: 1px solid black;
	line-height: 50px;
	text-align: center;
}

.enter:hover{
	background-color: #B8B8B8;
}

.wrapper-for-button{
	display: flex;
	justify-content: center;
}

a{
	text-decoration: none;
	color: black;
}
.choose-avatar{
	width: 100%;
	max-height: 170px;
	background-color: green;
	text-align: center;
	box-sizing: border-box;
	border: 3px solid black;
}
.choose-avatar p{
	margin-top: 0px;
	font-weight: bold;
}
.lovButton{
	display: block;
	margin-bottom: 20px !important;
	box-sizing: border-box;
}
.close{
	position: relative;
	left: 138px;
	bottom: 3px;
}

.choose-avatar div{
	display: flex;
	flex-direction: column;
	align-items: center;

}
.choose-avatar div button{
	margin-bottom: 20px;
	width: 80%;
}
body {
  margin: 0;
  padding: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

#root{
  width: 100vw;
	height: 100vh;
  display: flex;
  justify-content: center;
  align-items: center;
  background-image: url(/static/media/react.57e97fc2.jpg);
  background-repeat: no-repeat; 
}

.main{
	height: 80vh;
	width: 40vw;
	border: 1px solid black;
	background-color: white;
}

.inputField{
	height: 75%;
	width: 100%;
	margin: 0 auto;
	margin-bottom: 1%;
	border-bottom: 1px solid black;

}

.chatField{
	width: 98%;
	height: 20%;
	margin: 0 auto;
	display: flex;
}

.wrapper-for-avatar{
	margin: 5px;
	max-width: 50px;
	max-height: 50px;
	width: 10%;

}

.wrapper-for-message{
	margin-top: 5px;
	width: 85%;
	height: 90%;
}

.defaultAvatar,
.message{
	width: 100%;
	height: 100%;
	box-sizing: border-box;
}

.nameLastName{
	display: flex;
	flex-direction: row;
}

.nameLastName p{
	margin: 0px;
	margin-right: 10px;
}



